@use "sass:map";

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

$color-1: #fff18c;
$color-2: rgb(255, 244, 162);

html {
  height: 100%;
}

html,
body {
  font-family: "Scheherazade New", serif !important;
  font-size: 1.6vh !important;
  direction: rtl;
  height: 100%;
  user-select: none;
}

body {
  margin: 0;
  background-color: #535659;
}

* {
  box-sizing: border-box !important;
}

$colors: (
  "1": #dc0000,
  "2": #c5d7ed,
  "3": #f8e69d,
  "4": #eccbae,
  "5": #b3901c,
  "6": #d8dce5,
  "7": #3d5595,
  "8": #283863,
  "9": #aeabab,
  "10": #af5a19,
  "11": #63309d,
  "12": #63b0ee,
  "13": #63b057,
  "14": #435727,
  "15": #b3901c,
  "16": #e6f0da,
  "17": #ffff39,
  "18": #ccccfd,
  "19": #faf3ed,
);

$red: #dc0000;
$green: #00983a;
$blue: #4a559e;
$bs: #333 1px 1px 10px -4px;

// fonts
@import url("https://fonts.googleapis.com/css2?family=Scheherazade+New:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@0,400;0,500;0,600;1,400&family=Scheherazade+New:wght@400;700&display=swap");

#root {
  height: 100%;
}

.page {
  height: 100%;
  border: 1rem solid $red;
  max-width: 70.707vh;
  margin: auto;
  box-shadow: black 0px 0px 19px -4px;
  background-color: white;
  display: flex;
  flex-direction: column;
  .container {
    width: 100%;
    height: 100%;
    position: relative;
  }
  // overflow: hidden;
}

/****
 Teacher note
*/
.teacher-btn {
  position: absolute;
  left: 0.5rem;
  top: 0.5rem;
  width: 2.5rem;
  height: 2.5rem;
  background: #ffffff;
  border: 0.3rem #333 solid;
  color: #333;
  border-radius: 50%;
  font-size: 1.9rem;
  line-height: 1.9rem;
  text-align: center;
  font-weight: bold;
  font-family: Arial, sans-serif;
  cursor: pointer;
  &:hover,
  &:active {
    background-color: #e2e2e2;
  }
}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #32323269;
  z-index: 100;
  left: 0;
  top: 0;
  .box {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    width: 90%;
    max-width: 50rem;
    padding: 1rem;
    background-color: white;
    box-shadow: $bs;
    animation: fade 1s ease forwards;

    h2 {
      font-size: 1.3rem;
    }
  }
}

/*****
 Index page
*/

.index-btn {
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
  width: 2.7rem;
  height: 2.7rem;
  background-color: #f7e561;
  border: 0.3rem solid #696969;
  border-radius: 50%;
  text-align: center;
  cursor: pointer;
  z-index: 1000;
  &:hover,
  &:active {
    background-color: #e7d548;
  }
  span {
    // width: 80%;
    height: 0.3rem;
    width: 1.4rem;
    left: 0.36rem;
    top: 0.4rem;
    display: inline-block;
    position: absolute;
    // top: 1rem;
    background-color: #333333;
    border-radius: 1rem;
    transition: 0.2s ease-in-out;
  }
  span:nth-child(2) {
    top: 0.85rem;
  }
  span:nth-child(3) {
    top: 1.3rem;
  }
  &.open {
    span:nth-child(2),
    span:first-child {
      transform: rotate(45deg);
    }
    span:first-child {
      top: 0.9rem;
    }
    span:nth-child(3) {
      transform: rotate(-45deg);
      top: 0.9rem;
    }
  }
}
.index-page {
  max-width: 70.707vh;
  height: 100%;
  background-color: rgb(236, 236, 236);
  margin: auto;
  animation: fade 0.5s ease forwards;
  .header {
    h1 {
      margin: 0;
      text-align: center;
      padding: 1rem 1rem 0 1rem;
      font-size: 2rem;
    }
    h5 {
      margin: 0;
      font-size: 1rem;
      text-align: center;
    }
  }
  .container {
    padding: 2rem 2rem 0 0;
    display: flex;
    flex-wrap: wrap;
    overflow-y: scroll;
    height: 87%;
    .letter-box:nth-child(1) .letter {
      border-color: map-get($colors, "1");
    }
    .letter-box:nth-child(n + 2):nth-child(-n + 5).letter {
      border-color: map-get($colors, "2");
    }
    .letter-box:nth-child(n + 5):nth-child(-n + 8) .letter {
      border-color: map-get($colors, "3");
    }
    .letter-box:nth-child(n + 8):nth-child(-n + 10) .letter {
      border-color: map-get($colors, "4");
    }
    .letter-box:nth-child(n + 10):nth-child(-n + 12) .letter {
      border-color: map-get($colors, "5");
    }
    .letter-box:nth-child(n + 12):nth-child(-n + 14) .letter {
      border-color: map-get($colors, "6");
    }
    .letter-box:nth-child(n + 14):nth-child(-n + 16) .letter {
      border-color: map-get($colors, "7");
    }
    .letter-box:nth-child(n + 15):nth-child(-n + 17) .letter {
      border-color: map-get($colors, "8");
    }
    .letter-box:nth-child(n + 18):nth-child(-n + 20) .letter {
      border-color: map-get($colors, "9");
    }
    .letter-box:nth-child(n + 20):nth-child(-n + 22) .letter {
      border-color: map-get($colors, "10");
    }
    .letter-box:nth-child(22) .letter {
      border-color: map-get($colors, "11");
    }
    .letter-box:nth-child(23) .letter {
      border-color: map-get($colors, "12");
    }
    .letter-box:nth-child(24) .letter {
      border-color: map-get($colors, "13");
    }
    .letter-box:nth-child(25) .letter {
      border-color: map-get($colors, "14");
    }
    .letter-box:nth-child(26) .letter {
      border-color: map-get($colors, "15");
    }
    .letter-box:nth-child(27) .letter {
      border-color: map-get($colors, "16");
    }
    .letter-box:nth-child(28) .letter {
      border-color: map-get($colors, "17");
    }
    .letter-box:nth-child(29) .letter {
      border-color: map-get($colors, "18");
    }
    .letter-box:nth-child(30) .letter {
      border-color: map-get($colors, "19");
    }
    .letter-box:nth-child(31) .letter {
      border-color: map-get($colors, "19");
    }

    // .letter-box:nth-child(7),
    // .letter-box:nth-child(19),
    // .letter-box:nth-child(21),
    // .letter-box:nth-child(n + 14):nth-child(-n + 17) {
    //   .letter {
    //     border-color: $blue;
    //     color: $blue;
    //   }
    // }
    .letter-box {
      width: 33.333333%;
      height: 8rem;
      padding: 0 0 2rem 2rem;

      .letter {
        border-radius: 1rem;
        border: 0.3rem solid;
        border-color: rgb(139, 139, 139);
        text-align: center;
        height: 100%;
        padding: 1rem 0;
        white-space: nowrap;
        line-height: 2rem;
        font-size: 1.4rem;
        font-weight: bold;
        background-color: #fdfdfd;
        cursor: pointer;
        position: relative;
        box-shadow: $bs;
        &:hover,
        &:active {
          background-color: #cccccc;
        }
        .page-num {
          line-height: 1rem;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          bottom: 0.5rem;
          font-size: 1.1rem;
          color: #333;
        }
      }
    }
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.page-number {
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
  margin: 0;
}

@each $color-name, $color-key in $colors {
  .page.color_#{$color-name} {
    border-color: $color-key;
  }
}

.page > .container > .header {
  h1 {
    font-size: 1rem;
    font-weight: normal;
    text-align: center;
  }
}

span.red {
  color: $red;
}
span.green {
  color: #00983a;
}
span.blue {
  color: #4a559e;
}

span.mq {
  position: relative;
  left: 1rem;
  top: 0.5rem;
  font-weight: normal;
}

$wbr: $color-1 solid 0.3rem;
$wbr-2: #adadad solid 0.3rem;
/******
  Char page
*/
$bgcard: #f7f7f7;
$bgcard-hover: #e2e2e2;
.char-page {
  display: flex;
  flex-direction: column;
  flex: 1 auto;
  position: relative;
  height: 95%;
  padding-top: 1rem;
  > .row {
    position: relative;
    text-align: center;
    width: 80%;
    margin: 0 auto;
  }
  .char-title {
    width: 100%;
    text-align: center;
    font-size: 2.7rem;
    font-weight: bold;
    padding: 0 1rem;
    border-radius: 1rem;
    border: $wbr;
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    background-color: $bgcard;
    box-shadow: $bs;
    user-select: none;
    text-align: center;
  }

  .num {
    text-align: center;
    font-size: 1.7rem;
    display: flex;
    align-items: center;
    font-weight: bold;
    display: inline-block;
    border-radius: 50%;
    font-weight: bold;
    width: 4.2rem;
    height: 4.2rem;
    background-color: $red;
    color: #f1f1f1;
    border: 0.3rem solid #bebebe;
    margin-right: 2.5rem;

    &.blue {
      background-color: #133459;
    }
  }

  .content-container {
    width: 80%;
    margin: 0 auto;
    .row {
      display: flex;
      padding-bottom: 3rem;
      .img-container,
      .letter-container {
        width: 50%;
        padding: 2rem 0;
        .box {
          height: 100%;
          border-radius: 1rem;
          border: $wbr;
          box-shadow: $bs;
        }
      }
      .img-container {
        padding-right: 1rem;
        position: relative;
        .box {
          overflow: hidden;
          text-align: center;
          display: flex;
          justify-content: center;
          align-items: center;
        }
        .english-word {
          display: block;
          position: absolute;
          left: 50%;
          bottom: -1rem;
          padding: 0rem 2rem;
          background-color: #f7f7f7;
          font-size: 1.4rem;
          transform: translateX(-50%) translateX(-0.5rem);
          border-radius: 1rem;
          border: $wbr;
        }
        img {
          height: 11rem;
        }
      }
      .letter-container {
        padding-left: 1rem;
        font-size: 7rem;
        font-weight: bold;
        .box {
          background-color: $color-2;
          border: $wbr-2;
          cursor: pointer;
          user-select: none;
          &:active {
            background-color: #e4de98;
          }
          span {
            display: block;
            width: 100%;
            height: 100%;
            line-height: 13rem;
            text-align: center;
            margin: auto;
          }
        }
      }
      .word-container {
        width: 100%;
        .box {
          font-size: 7rem;
          text-align: center;
          background-color: $color-2;
          border: $wbr-2;
          cursor: pointer;
          user-select: none;
          border-radius: 1rem;
          box-shadow: $bs;
          font-weight: bold;
          &:active {
            background-color: #e4de98;
          }
        }
      }
    }
  }
}

/*******
  Teacher page
*/
.teacher-stuff-page {
  padding: 0 1.5rem 1.5rem 1.5rem;
  font-size: 1.2rem;
  font-weight: bold;
  line-height: 2rem;
  ul {
    padding: 0;
    padding: 0 1rem;
    margin: 0;
  }
  h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
    text-align: center;
  }
}

/*******
  Global classes
*/
.eng {
  font-family: "EB Garamond", serif;
}

.italic {
  font-style: italic;
}
@each $color-name, $color-key in $colors {
  .page.color_#{$color-name} .button {
    background: $color-key;
  }
}

.nav-button {
  position: absolute;
  bottom: 0.5rem;
  height: 2.5rem;
  width: 2.5rem;
  background-color: #fcf4a6;
  border: 0.2rem solid #adadad;
  z-index: 10;
  border-radius: 50%;
  cursor: pointer;
  &:hover,
  &:active {
    .arrow {
      &:after,
      &:before {
        background-color: #444444;
      }
    }
  }
  &.right {
    right: 30%;

    .arrow {
      &:before,
      &:after {
        left: 58%;
        top: 34%;
        transform: translateX(-50%) rotate(55deg);
      }
      &:after {
        top: 7%;
        transform: translateX(-50%) rotate(-55deg);
      }
    }
  }
  &.left {
    left: 30%;

    .arrow {
      &:before,
      &:after {
        left: 45%;
        top: 34%;
        transform: translateX(-50%) rotate(-55deg);
      }
      &:after {
        top: 7%;
        transform: translateX(-50%) rotate(55deg);
      }
    }
  }

  .arrow {
    &:before,
    &:after {
      content: "";
      position: absolute;
      width: 0.3rem;
      border-radius: 1rem;
      height: 60%;
      background: #7c7c7c;
      transform: translateX(-50%) rotate(-55deg);
    }
  }
}

/*******
  All letters page
*/
.all-letters-page {
  .letters-container {
    display: flex;
    flex-wrap: wrap;
    padding-right: 1rem;
    justify-content: center;

    .letter:first-child {
      .box {
        color: $red;
      }
    }
    .letter:nth-child(32) {
      .box {
        border-radius: 50%;
        font-size: 1.3rem;
        background-color: #b1b1b1;
        border-color: #fde973;
        border-width: 0.3rem;
        line-height: 2.5rem;
        color: white;
        cursor: initial;
        &:active {
          background-color: #b1b1b1;
        }
      }
    }
    .letter {
      width: 20%;
      padding: 1rem 0 0 1rem;
      .box {
        margin: auto;
        width: 100%;
        text-align: center;
        font-size: 2.8rem;
        line-height: 5.4rem;
        height: 5.7rem;
        border-radius: 1rem;
        border: #b2b2b2 solid 0.2rem;
        font-weight: bold;
        color: black;
        background-color: #fff3a1;
        cursor: pointer;
        user-select: none;
        position: relative;
        &:active {
          background-color: #d4cd87;
        }
        .number {
          position: absolute;
          left: 0.2rem;
          top: 0.2rem;
          font-size: 1rem;
          height: 1.4rem;
          width: 1.4rem;
          border-radius: 50%;
          line-height: 1.5rem;
          color: white;
          &.red {
            background-color: $red;
          }
          &.blue {
            background-color: $blue;
          }
        }
      }
    }
  }
}
